<template>
	<div>
		<!-- 按钮 -->
		<button :class="['buttonBorder',!_rotate?'dlbutton':'dlbutton_loading']" :style="{'background':bgColor, 'color': fontColor}">
			<div :class="_rotate?'rotate_loop':''">
				<p v-if="_rotate" class="cuIcon cuIcon-loading1 "></p>
				<p v-if="!_rotate">{{ text }}</p>
			</div>
		</button>
	</div>
</template>

<script>
	export default{
		props:{
			text: String, //显示文本
			rotate:{
				//是否启动加载
				type: [Boolean,String],
				default: false,
			}, 
			bgColor:{
				//按钮背景颜色
				type: String,
				default: " linear-gradient(to right, #00634f , #0b8560, #2bd396 )",
			},
			fontColor:{
				//按钮字体颜色
				type: String,
				default: "#FFFFFF",
			},
		},
		computed:{
			_rotate() {
				//处理值
				return String(this.rotate) !== 'false'
			},
		}
	}
</script>

<style>
 @import url("./css/icon.css"); 
	button{
		cursor: pointer;
	}	
	.dlbutton {
		display: flex;
		justify-content: center;
		align-items: center;
		color: #FFFFFF;
		font-size: 15px;
		width:100%;
		height:40px;
		background: #11998e;
		background:linear-gradient(to right, #00634f,#0b8560,  #2bd396);
		box-shadow:0px 0px 8px 0px rgba(164,217,228,0.4);
		border-radius:12%;
		margin-top: 0px;
	}
	.dlbutton_loading {
		display: flex;
		justify-content: center;
		align-items: center;
		color: #FFFFFF;
		font-size: 23px;
		width:100%;
		height:40px;
		background: linear-gradient(to right, #00634f ,#0b8560,  #2bd396);
		box-shadow:0px 0px 8px 0px rgba(164,217,228,0.4);
		border-radius:2.5rem;
		margin-top: 0px;
	}
	.buttonBorder{
	    border: none ;
		border-radius: 2.5rem ;
		outline: none;
	    -webkit-box-shadow: 0 0 20px 0 rgba(0,0,0,.2) ;
	    box-shadow: 0 0 20px 0 rgba(0,0,0,.2) ;
	    -webkit-transition: all 0.4s cubic-bezier(.57,.19,.51,.95);
	    -moz-transition: all 0.4s cubic-bezier(.57,.19,.51,.95);
	    -ms-transition: all 0.4s cubic-bezier(.57,.19,.51,.95);
	    -o-transition: all 0.4s cubic-bezier(.57,.19,.51,.95);
	    transition: all 0.4s cubic-bezier(.57,.19,.51,.95);
	}
	
	/* 旋转动画 */
	.rotate_loop{
		-webkit-transition-property: -webkit-transform;
	    -webkit-transition-duration: 1s;
	    -moz-transition-property: -moz-transform;
	    -moz-transition-duration: 1s;
	    -webkit-animation: rotate 1s linear infinite;
	    -moz-animation: rotate 1s linear infinite;
	    -o-animation: rotate 1s linear infinite;
	    animation: rotate 1s linear infinite;
	}
	@-webkit-keyframes rotate{from{-webkit-transform: rotate(0deg)}
	    to{-webkit-transform: rotate(360deg)}
	}
	@-moz-keyframes rotate{from{-moz-transform: rotate(0deg)}
	    to{-moz-transform: rotate(359deg)}
	}
	@-o-keyframes rotate{from{-o-transform: rotate(0deg)}
	    to{-o-transform: rotate(359deg)}
	}
	@keyframes rotate{from{transform: rotate(0deg)}
	    to{transform: rotate(359deg)}
	}
</style>
